* {
	box-sizing: border-box;
}

textarea:focus, input:focus {
    outline: none;
}

body {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	background-color: #f7f8f;
	display: flex;
	font-family: Arial, Helvetica, sans-serif;
	height: 100vh;
	margin: 0;
	overflow-x: hidden;
	padding: 0;
	width: 100%;
}

#root {
	width: 100%;
}
